/*
------------------------------------------
Email Form side
------------------------------------------
*/
#EmailForm {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 45%;
}
p {
	width: 100%;
	text-align: center;
	margin: auto;
	padding-top: 30px;
	padding-bottom: 15px;
	font-size: 1em;
	line-height: 1.5em;
}
form {
	width: 100%;
}
#EmailForm h3 {
	font-family: "Raleway", sans-serif;
	font-weight: bold;
	font-size: 3em;
	line-height: 1em;
	padding-bottom: 30px;
}

#hr2 {
	margin: auto;
	width: 2.5em;
	border: 0.1em solid #f4bd16;
	background-color: #f4bd16;
}

/*
------------------------------------------
TextBoxes
------------------------------------------
*/
form p {
	display: table;
	padding-top: 5px;
	padding-bottom: 5px;
}
input {
	height: 4vw;
}
textarea {
	height: 30vw;
}
input,
textarea {
	display: table;
	width: 100%;
	border: 1px solid #dddddd;
	font-family: "Work Sans", sans-serif;
	box-sizing: border-box;
	border-radius: 10px;
	font-family: "Raleway";
	font-style: normal;
	color: black;
	font-size: 1.5vw;
}
#button {
	width: 13%;
	border-radius: 10px;
	background: #004c6d;
	font-weight: 600;
	color: white;
}

::placeholder {
	line-height: 200%;
}

/* Mobile Styles */
@media only screen and (max-width: 420px) {
	#EmailForm {
		width: 100%;
		padding-top: 50px;
	}
}

/* Tablet Styles */
@media only screen and (min-width: 421px) and (max-width: 960px) {
	#EmailForm {
		width: 100%;
		padding-top: 50px;
	}
}

/* Desktop Styles */
@media only screen and (min-width: 961px) {
	input,
	#button {
		height: 3vw;
	}
	textarea {
		height: 25vw;
	}
}
