.about-title {
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-image: linear-gradient(
			rgba(14, 51, 72, 0.95),
			rgba(14, 51, 72, 0.95)
		),
		url("../media/workshop.jpg");
	background-repeat: no-repeat;
	background-size: cover;
}

.about-title h1 {
	font-weight: 400;
	font-size: 3em !important;
	line-height: 1em;
	color: white;
	margin: 0;
}

.about-title hr {
	width: 2.5em;
	background-color: white;
	border: 0.1em solid white;
	margin: 2em 0;
}
.story-about-hr {
	margin: 0 !important;
}
.about-title .button {
	font-size: 1.25vw;
	padding: 0.4vw 1.1vw;
	border-radius: 7px;
	background-color: #f4bd16;
	display: flex;
	justify-content: center;
	align-items: center;
}

.about-title a {
	color: black;
	text-decoration: none;
}

.about-content {
	height: 100%;
	margin: 3% 0 !important;
	padding: 3% 15%;
	display: flex;
	flex-direction: column !important;
}

.about-content h2 {
	text-align: center;
	font-size: 3.5vw;
}

.about-content hr {
	width: 6%;
	margin: 4% auto 2% auto;
	border-bottom: 2px solid black;
}

.about-content p {
	font-size: 1.5vw;
	padding: 2% 0;
}

.workshop {
	width: 100%;
	padding: 6% 0;
}

.dunin {
	margin-bottom: 50px;
	width: 80%;
}

.content a {
	text-decoration: none;
	color: blue;
}

.content a:hover {
	text-decoration: underline;
}

/* Mobile Styles */
@media only screen and (max-width: 420px) {
	.about-title h1 {
		font-size: 8vw !important;
	}

	.about-title hr {
		width: 8%;
		margin: 6% 0;
	}

	.about-title .button {
		font-size: 2.5vw;
	}

	.workshop,
	.tours {
		width: 100%;
	}

	.tours {
		padding-bottom: 0%;
	}
}

/* Tablet Styles */
@media only screen and (min-width: 421px) and (max-width: 960px) {
	.about-title h1 {
		font-size: 6vw !important;
	}

	.about-title hr {
		width: 8%;
		margin: 5% 0;
	}

	.about-title .button {
		font-size: 2vw;
	}

	.workshop,
	.tours {
		width: 100%;
	}

	.tours {
		padding-bottom: 0%;
	}
}

/* Desktop Styles */
@media only screen and (min-width: 961px) {
	.workshop hr,
	.tours hr {
		margin: 6% auto 2% auto;
	}
}
