	background: -webkit-gradient(linear, left top, left bottom, from(rgba(14, 51, 72, 0.9)), to(rgba(14, 51, 72, 0.9))),
		url(/static/media/WindowPicture.bb9ef7c8.jpg);
	background: linear-gradient(rgba(14, 51, 72, 0.9), rgba(14, 51, 72, 0.9)),
		url(/static/media/WindowPicture.bb9ef7c8.jpg);
	background-size: cover;
	background-position: 50% 60%;
	z-index: 2;
}
#FrontText {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100vh;
}
.contact-title {
	font-size: 3em;
	font-weight: 400 !important;
	line-height: 1em;
}
.contact-hr {
	width: 2.5em;
	background-color: white;
	border: 0.1em solid white;
	margin: 2em 0;
}
#ContactUs {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
}
#container {
	width: 90%;
	display: flex;

	margin: 5% 0;
	align-items: flex-start;
	justify-content: space-around;
}

/*
------------------------------------------
Map Side
------------------------------------------
*/
.locateUs {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	list-style-type: none;
}

#GoogleMap {
	width: 35%;
	font-family: "Nunito", sans-serif;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}
#GoogleMap h3 {
	line-height: 1em;
	font-family: "Raleway", sans-serif;
	font-weight: bold;
	font-size: 3em;
	padding-bottom: 30px;
}

#hr1 {
	margin: auto;

	width: 2.5em;

	border: 0.1em solid #f4bd16;
	background-color: #f4bd16;
}

#GoogleMap p {
	display: table;
	padding-top: 15px;
	padding-bottom: 10px;
	font-size: 2vw;
}
iframe {
	display: table;
	padding-top: 15px;
	padding-bottom: 5px;
	float: right;
}

/* Mobile Styles */
@media only screen and (max-width: 420px) {
	#container {
		display: flex;
		margin: 8% 11% 8% 11%;
		flex-wrap: wrap;
	}
	#GoogleMap {
		width: 100%;
		height: 100%;
		z-index: 0;
		font-family: "Nunito", sans-serif;
	}
}

/* Tablet Styles */
@media only screen and (min-width: 421px) and (max-width: 960px) {
	#container {
		display: flex;
		margin: 8% 11% 8% 11%;
		flex-wrap: wrap;
	}
	#GoogleMap {
		width: 100%;
	}
}
