.App {
	text-align: center;
}
.navContainer {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: flex-start;
	z-index: 3;
	position: absolute;
	margin-top: 50px;
	width: 100%;
}

.dunin-logo {
	width: 30%;
}

.app-logo {
	text-align: left;

	color: white;
}

/* Mobile Styles */
@media only screen and (max-width: 649px) {
	.navContainer {
		margin-top: 30px;
		flex-direction: column;
	}
	.app-logo {
		margin-left: 50px;
	}
}

/* Tablet Styles */
@media only screen and (min-width: 650px) and (max-width: 1400px) {
	.navContainer {
		width: 100%;
		margin-top: 30px;
		flex-wrap: wrap;
		justify-content: flex-start;
	}
	.app-logo {
		margin-left: 50px;
		width: 100%;
	}
}
/* Desktop Styles */
