.footer {
	padding: 20px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	flex-wrap: wrap;
	align-items: center;
	width: 100%;
}

.media-links {
	display: flex;
	width: 250px;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	color: #0b2230;
}

.sparQ-logo {
	width: 100px;
	height: 50px;
}
.copyright {
	margin-left: 10px;
	width: 150px;
	height: 50px;
}

/* Mobile Styles */
@media only screen and (max-width: 420px) {
	.sparQ-logo {
		visibility: hidden;
	}
	.footer {
		flex-direction: column;
		justify-content: center;
	}

	.media {
		width: 16%;
	}
}

