nav {
	display: flex;
	justify-content: flex-end;
	flex-direction: row;
	width: 40%;
}
.pages {
	display: none;
}
.navitems {
	margin-right: 75px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-direction: row;
	width: 100%;
}
.link-item {
	padding-top: 10px;
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	color: white;
	text-decoration: none;
}
.home,
.about,
.contact {
	color: white;
}
.memberships {
	color: black;
}
.link-item-active {
	font-weight: bold;
	text-decoration: underline;
}
.navbar-toggle {
	height: 1em;
	width: 1em;
	display: flex;
	justify-content: space-evenly;
	flex-direction: column;
	align-self: flex-end;
	margin-top: -30px;
	cursor: pointer;
	font-size: 40px;
	z-index: 1;
}

.dot {
	align-self: left;
	width: 8px;
	height: 8px;
	background-color: white;
	border-radius: 4px;
}
.dot-memberships {
	background-color: black;
}
.active {
	display: block;
}
.activeContainer {
	width: 100%;
	display: block;
}
/* Mobile Styles */
@media only screen and (max-width: 649px) {
	@keyframes slide {
		0% {
			top: 500px;
		}
		100% {
			left: 0px;
			top: 0px;
		}
	}
	nav {
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		width: 100%;
		margin-top: 5vh;
	}
	.navitems {
		position: relative;
		animation-name: slide;
		animation-duration: 1.3s;
		animation-direction: normal;
	}
	.active {
		margin: 0;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		flex-direction: column;
		z-index: 1;
		height: 100vh;
		background-color: rgba(250, 249, 249, 0.9);
	}
	.link-item {
		color: #0b2230;
		margin-top: 30px;
	}
}

/* Tablet Styles */
@media only screen and (min-width: 650px) and (max-width: 1400px) {
	.pages {
		display: block;
	}
	.active {
		display: flex;
	}
	nav {
		width: 100%;
	}
	.navitems {
		margin-top: 25px;
		margin-right: 0;
		width: 100vw;
		justify-content: space-around;
	}
	.navbar-toggle {
		display: none;
	}
}
/* Desktop Styles */
@media only screen and (min-width: 1401px) {
	.pages {
		display: block;
	}
	.active {
		display: flex;
	}
	.navbar-toggle {
		display: none;
	}
}
