.membership-title {
	height: 100vh;
	background: linear-gradient(
			rgba(255, 255, 255, 0.8),
			rgba(255, 255, 255, 0.8)
		),
		url(.././media/IMG_8867.jpg);
	background-size: cover;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	padding: 0 13%;
}

.membership-title h1 {
	color: black !important;
	font-weight: 400;
	font-size: 3em !important;
	line-height: 1em;
	margin: 0;
}

.membership-title hr,
.equipment hr {
	width: 2.5em;
	background-color: black;
	border: 0.1em solid black;
	margin: 2em 0;
}

.membership-title h2 {
	margin: 0 5% 2% 0;
	font-weight: 450;
	font-size: 1.4vw;
	line-height: 1.5;
	text-align: left;
}

.membership-title .button {
	margin-top: 1%;
	font-size: 1.25vw;
	padding: 0.4vw 1.1vw;
	border-radius: 7px;
	background-color: #113244;
	display: flex;
	justify-content: center;
	align-items: center;
}

.membership-title a {
	color: white;
	text-decoration: none;
}

.container {
	width: 100%;
	display: flex;
	flex-direction: column;
}

.membership {
	height: 100%;
	padding: 5% 0;
	background-color: white !important;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.membership .table-title {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 1% 5%;
}

.membership label {
	font-size: 3.5vw;
	font-weight: bold;
	text-align: left !important;
}

.membership select {
	font-size: 2vw;
	font-weight: bold;
	border: 0px;
	background-color: white;
	text-align: right !important;
}

.membership table {
	width: 85%;
	margin: 20px auto;
	border-collapse: collapse;
}

th,
td {
	border-bottom: 1px solid rgb(150, 150, 150);
	text-align: center;
	font-size: 1.5vw;
	padding: 1.25vw 1.75vw;
}

.info,
.equipment a {
	text-decoration: none;
	color: rgb(0, 76, 109);
}

.purchase-online {
	background-color: #f4bd16;
	border-radius: 7px;
	border: none;
	padding: 0.3em 0.5em;
	text-decoration: none;
	display: inline-block;
	z-index: 1;
	transition: 0.3s;
}
.info:hover,
.membership-table a:hover,
.equipment a:hover {
	text-decoration: underline;
}

.membership h2 {
	font-size: 1.5vw;
	padding: 2% 9%;
	text-align: left;
}

.membership p {
	padding: 0 9%;
	font-size: 1.25vw;
	text-align: left;
}

.equipment {
	padding: 2% 9%;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
}

.equipment-container {
	width: 50%;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
}

.equipment-container h2 {
	font-size: 3.5vw;
	text-align: left;
}

.equipment-title {
	font-size: 1.33vw;
	text-align: left;
	padding: 0 0 2% 0;
}

.menu {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
}

.dropdown-btn {
	width: 17.5vw;
	height: 4vw;
	text-decoration: none;
	text-align: left !important;
	border: none;
	font-size: 1.33vw;
	font-weight: bold;
	font-family: Nunito;
	background-color: white;
}
.purchase-online a {
	font-size: 1.2vw;
	color: black;
	text-decoration: none;
}
.dropdown-btn:focus,
select:focus {
	outline: 0;
}

.dropdown-container li {
	width: 17.5vw;
	text-decoration: none;
	list-style: none;
	text-align: left;
	line-height: 2.5vw;
	font-size: 1.2vw;
	font-family: Nunito;
	padding-left: 3vw;
}

.dropdown-btn i {
	font-size: 1.33vw;
	color: black;
	padding-right: 2vw;
}

.equipment-pictures {
	width: 30%;
	padding-left: 2%;
}

/* Mobile Styles */
@media only screen and (max-width: 420px) {
	.membership-title h1 {
		font-size: 8vw !important;
	}

	.membership-title hr {
		width: 12%;
		margin: 5% 0;
	}

	.membership-title h2 {
		font-size: 2.5vw;
	}

	.membership-title .button {
		font-size: 2.25vw;
	}

	.equipment {
		flex-direction: column;
	}

	.equipment-container {
		width: 100%;
	}

	.dropdown-btn {
		width: 25vw;
	}

	.dropdown-container li {
		width: 17.5vw;
		height: 2.5vw;
	}

	.equipment-pictures {
		width: 75%;
		padding: 0;
		margin: 4% auto;
	}
}

/* Tablet Styles */
@media only screen and (min-width: 421px) and (max-width: 960px) {
	.membership-title h1 {
		font-size: 6vw !important;
	}

	.membership-title h2 {
		font-size: 2vw;
	}
}

