@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

body {
	font-size: 3vw;
	line-height: 1.5em;
}

.video {
	overflow: hidden;
	position: relative;
	width: 100%;
	height: 100%;
}

.vidText {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	position: absolute;
	color: white;
	background-color: rgba(14, 51, 72, 0.9);
	width: 100%;
	height: 100%;
}

.title {
	font-size: 3em;
	line-height: 1em;
	color: white;
}
.sub {
	color: black;
}
.hr {
	width: 2.5em;
	background-color: white;
	border: 0.1em solid white;
	margin: 2em 0;
}
.home-heading {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	flex-wrap: wrap;
	width: 40%;
}
.subtitle {
	font-size: 1em;
	font-family: "Nunito", sans-serif;
	margin-bottom: 2em;
	width: 75%;
}

.button {
	font-family: "Raleway", sans-serif;
	font-size: 1em;
	font-weight: bold;
	background-color: #f4bd16;
	border-radius: 7px;
	border: none;
	color: #060707;
	padding: 0.5em 1em;
	text-decoration: none;
	display: inline-block;
	z-index: 1;
	transition: 0.3s;
}

.button:hover {
	opacity: 0.7;
}

.bgVideo {
	height: 100vh;
}

.content {
	width: 100%;
	margin-top: 8em;
	text-align: left;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: space-evenly;
	flex-direction: row-reverse;
}

.two {
	align-items: flex-start;
	width: 80%;
	align-self: center;
}
.hr2 {
	width: 2.5em;
	background-color: black;
	border: 0.1em solid black;
	margin: 2em 0;
}

.subtitle2 {
	text-align: left;
	line-height: 1.5em;
	font-size: 1em;
	font-family: "Nunito", sans-serif;
	margin-bottom: 2em;
}

img {
	width: 75%;
	transition: 0.5s ease-in-out;
}

img:hover {
	opacity: 0.5;
}

.banner {
	background-image: linear-gradient(
			rgba(14, 51, 72, 0.95),
			rgba(14, 51, 72, 0.95)
		),
		url("../media/IMG_8867.jpg");

	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	color: white;
	background-color: rgba(14, 51, 72, 0.9);
	height: 20em;
	margin-top: 8em;
	margin-bottom: 8em;
}

.bannerText {
	font-family: "Raleway", sans-serif;
	font-weight: bold;
	font-size: 2em;
	margin-bottom: 1.5em;
	line-height: 1.2em;
	width: 80%;
}

/* Mobile Styles */
@media only screen and (max-width: 420px) {
	.row {
		flex-direction: column;
	}
	.home-heading {
		margin-top: 4em;
		width: 80%;
		margin-bottom: 0;
	}
	.two {
		margin-top: 0;
	}
	.title2 {
		font-size: 11vw;
	}
}

/* Tablet Styles */
@media only screen and (min-width: 421px) and (max-width: 960px) {
	body {
		font-size: 2.5vw;
	}
	.content {
		margin-top: 4em;
	}
	.home-heading {
		margin-top: 4em;
		width: 80%;
		margin-bottom: 0;
	}
}

/* Desktop Styles */
@media only screen and (min-width: 961px) {
	body {
		font-size: 1.2vw;
	}

	.subtitle {
		width: 47%;
	}

	.bgVideo {
		height: 100%;
		width: 100%;
		margin-left: 0;
	}

	.imgCell {
		width: 25%;
	}

	.banner {
		flex-direction: row;
		height: 12em;
		padding: 0em 4em;
	}

	.bannerText {
		margin-right: 2em;
		text-align: left;
		margin-bottom: 0;
		width: 60%;
	}
}
